import { useState, useEffect, useCallback } from 'react';
import { fetchReviews, approveReviews as approveReviewsApi } from '../services/api';

/**
 * Custom hook to manage reviews state and operations
 */
export const useReviews = (filters = {}, sortBy = 'date') => {
  const [reviews, setReviews] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const loadReviews = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const data = await fetchReviews(filters, sortBy);
      setReviews(data.reviews);
    } catch (err) {
      setError(err.message || 'Failed to load reviews');
      console.error('Failed to load reviews:', err);
    } finally {
      setLoading(false);
    }
  }, [filters, sortBy]);

  // Load reviews on mount and when filters/sortBy change
  useEffect(() => {
    loadReviews();
  }, [filters, sortBy, loadReviews]);

  // Derive approved review ids from reviews
  const approvedReviews = reviews && Array.isArray(reviews)
    ? reviews.filter(r => r.approved).map(r => r.id)
    : [];


  const toggleApproval = useCallback(async (reviewId) => {
    try {
      const review = reviews.find(r => r.id === reviewId);
      if (!review) return;
      await approveReviewsApi([reviewId], !review.approved);
      await loadReviews();
    } catch (err) {
      console.error('Failed to toggle approval:', err);
    }
  }, [reviews, loadReviews]);

  const bulkApprove = useCallback(async (reviewIds, approved) => {
    try {
      await approveReviewsApi(reviewIds, approved);
      await loadReviews();
    } catch (err) {
      console.error('Failed to approve reviews:', err);
    }
  }, [loadReviews]);

  return {
    reviews,
    loading,
    error,
    approvedReviews,
    toggleApproval,
    bulkApprove,
    reloadReviews: loadReviews
  };
};
