import axios from 'axios';

const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
const api = axios.create({
  baseURL: API_BASE_URL,
});

api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

export const signIn = async (email, password) => {
  const response = await api.post('/manager/signin', { email, password });
  return response.data;
};

export const fetchReviews = async (filters = {}, sortBy = 'date') => {
  // Filter out empty values
  const cleanFilters = Object.keys(filters).reduce((acc, key) => {
    if (filters[key] !== '' && filters[key] !== 'all') {
      acc[key] = filters[key];
    }
    return acc;
  }, {});

  const params = {
    ...cleanFilters,
    sortBy
  };
  const response = await api.get('/reviews/hostaway', { params });
  return response.data;
};

export const approveReviews = async (reviewIds, approved) => {
  const user = localStorage.getItem('user');
  const parsedUser = user ? JSON.parse(user) : null;
  const managerId = parsedUser ? parsedUser.id : null;

  const response = await api.post('/manager/approve', {
    reviewIds,
    approved,
    managerId
  });
  return response.data;
};