import React, { useState } from 'react';
import { Card, Form, Input, Button, message } from 'antd';
import { updatePassword } from '../services/accountApi';

function AccountPage() {
  const [loading, setLoading] = useState(false);
  const [form] = Form.useForm();

  const onFinish = async (values) => {
    const userRaw = localStorage.getItem('user');
    const user = userRaw ? JSON.parse(userRaw) : null;
    const userId = user ? user.id : null;

    if (!userId) {
      message.error('User not signed in');
      return;
    }

    if (values.newPassword !== values.confirmPassword) {
      message.error('New password and confirmation do not match');
      return;
    }

    setLoading(true);
    try {
      const resp = await updatePassword(userId, values.currentPassword, values.newPassword);
      if (resp && resp.status === 'success') {
        message.success('Password updated');
        form.resetFields();
      } else {
        message.error(resp && resp.message ? resp.message : 'Failed to update password');
      }
    } catch (err) {
      console.error('Update password error', err);
      message.error('Failed to update password');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ maxWidth: 480, margin: '24px auto' }}>
      <Card title="Account">
        <Form form={form} layout="vertical" onFinish={onFinish}>
          <Form.Item name="currentPassword" label="Current password" rules={[{ required: true }]}>
            <Input.Password />
          </Form.Item>

          <Form.Item name="newPassword" label="New password" rules={[{ required: true, min: 6 }]}>
            <Input.Password />
          </Form.Item>

          <Form.Item name="confirmPassword" label="Confirm new password" rules={[{ required: true }]}>
            <Input.Password />
          </Form.Item>

          <Form.Item>
            <Button type="primary" htmlType="submit" loading={loading}>
              Update password
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
}

export default AccountPage;
