import React from 'react';
import './FilterBar.css';

function FilterBar({
  filters,
  setFilters,
  sortBy,
  setSortBy,
  uniqueListings,
  allCategories,
  uniqueTypes,
  uniqueChannels
}) {
  const handleFilterChange = (key, value) => {
    setFilters(key, value);
  };

  const clearFilters = () => {
    // Reset all filters by calling setFilters for each key
    setFilters('listing', '');
    setFilters('rating', '');
    setFilters('category', '');
    setFilters('channel', '');
    setFilters('type', '');
    setFilters('dateRange', 'all');
  };

  const hasActiveFilters = Object.values(filters).some(
    value => value !== '' && value !== 'all'
  );

  return (
    <div className="filter-bar">
      <div className="filter-group">
        <label>Property</label>
        <select
          className="select-field"
          value={filters.listing}
          onChange={(e) => handleFilterChange('listing', e.target.value)}
        >
          <option value="">All Properties</option>
          {uniqueListings.map(listing => (
            <option key={listing} value={listing}>
              {listing}
            </option>
          ))}
        </select>
      </div>

      <div className="filter-group">
        <label>Rating</label>
        <select
          className="select-field"
          value={filters.rating}
          onChange={(e) => handleFilterChange('rating', e.target.value)}
        >
          <option value="">All Ratings</option>
          <option value="8">8+ Stars</option>
          <option value="6">6+ Stars</option>
          <option value="4">4+ Stars</option>
          <option value="2">2+ Stars</option>
        </select>
      </div>

      <div className="filter-group">
        <label>Category</label>
        <select
          className="select-field"
          value={filters.category}
          onChange={(e) => handleFilterChange('category', e.target.value)}
        >
          <option value="">All Categories</option>
          {allCategories.map(category => (
            <option key={category} value={category}>
              {category.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())}
            </option>
          ))}
        </select>
      </div>

      <div className="filter-group">
        <label>Type</label>
        <select
          className="select-field"
          value={filters.type}
          onChange={(e) => handleFilterChange('type', e.target.value)}
        >
          <option value="">All Types</option>
          {uniqueTypes && uniqueTypes.map(type => (
            <option key={type} value={type}>
              {type.replace(/-/g, ' ').replace(/\b\w/g, l => l.toUpperCase())}
            </option>
          ))}
        </select>
      </div>

      <div className="filter-group">
        <label>Channel</label>
        <select
          className="select-field"
          value={filters.channel}
          onChange={(e) => handleFilterChange('channel', e.target.value)}
        >
          <option value="">All Channels</option>
          {uniqueChannels && uniqueChannels.map(channel => (
            <option key={channel} value={channel}>
              {channel.charAt(0).toUpperCase() + channel.slice(1)}
            </option>
          ))}
        </select>
      </div>

      <div className="filter-group">
        <label>Time Period</label>
        <select
          className="select-field"
          value={filters.dateRange}
          onChange={(e) => handleFilterChange('dateRange', e.target.value)}
        >
          <option value="all">All Time</option>
          <option value="week">Last 7 Days</option>
          <option value="month">Last 30 Days</option>
          <option value="quarter">Last 3 Months</option>
        </select>
      </div>

      <div className="filter-group">
        <label>Sort By</label>
        <select
          className="select-field"
          value={sortBy}
          onChange={(e) => setSortBy(e.target.value)}
        >
          <option value="date">Date (Newest)</option>
          <option value="rating">Rating (Highest)</option>
          <option value="listing">Property Name</option>
        </select>
      </div>

      {hasActiveFilters && (
        <button className="btn btn-secondary clear-filters" onClick={clearFilters}>
          Clear Filters
        </button>
      )}
    </div>
  );
}

export default FilterBar;

