import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Rate } from 'antd';
import { format } from 'date-fns';
import './ReviewList.css';

function ReviewList({ reviews, approvedReviews, onToggleApproval, onBulkApprove }) {
  const [selectedReviews, setSelectedReviews] = useState([]);

  const handleSelectAll = (checked) => {
    if (checked) {
      setSelectedReviews(reviews.map(r => r.id));
    } else {
      setSelectedReviews([]);
    }
  };

  const handleSelectReview = (reviewId, checked) => {
    if (checked) {
      setSelectedReviews([...selectedReviews, reviewId]);
    } else {
      setSelectedReviews(selectedReviews.filter(id => id !== reviewId));
    }
  };

  const handleBulkApproveAction = (approved) => {
    onBulkApprove(selectedReviews, approved);
    setSelectedReviews([]);
  };

  return (
    <div className="review-list-container">
      {selectedReviews.length > 0 && (
        <div className="bulk-actions">
          <span>{selectedReviews.length} selected</span>
          <button
            className="btn btn-success"
            onClick={() => handleBulkApproveAction(true)}
          >
            Approve Selected
          </button>
          <button
            className="btn btn-secondary"
            onClick={() => handleBulkApproveAction(false)}
          >
            Unapprove Selected
          </button>
          <button
            className="btn btn-secondary"
            onClick={() => setSelectedReviews([])}
          >
            Clear Selection
          </button>
        </div>
      )}

      {reviews.length === 0 ? (
        <div className="no-reviews">No reviews found matching your filters.</div>
      ) : (
        <>
          <div className="review-list">
            <div className="review-item review-header">
              <input
                type="checkbox"
                checked={selectedReviews.length === reviews.length && reviews.length > 0}
                onChange={(e) => handleSelectAll(e.target.checked)}
              />
              <div className="review-content-header">
                <div>Guest / Property</div>
                <div>Rating</div>
                <div>Categories</div>
                <div>Date</div>
                <div>Status</div>
              </div>
            </div>

            {reviews.map(review => (
              <div
                key={review.id}
                className={`review-item ${review.approved ? 'approved' : ''} ${
                  selectedReviews.includes(review.id) ? 'selected' : ''
                }`}
              >
                <input
                  type="checkbox"
                  checked={selectedReviews.includes(review.id)}
                  onChange={(e) => handleSelectReview(review.id, e.target.checked)}
                />
                <div className="review-content">
                  <div className="review-main">
                    <div className="review-guest">
                      <div className="guest-name">{review.guestName}</div>
                      <Link
                        to={`/property/${encodeURIComponent(review.listingName)}`}
                        className="listing-name-link"
                      >
                        {review.listingName}
                      </Link>
                      <div className="review-meta">
                        <span className="badge badge-secondary">{review.type}</span>
                        <span className="badge badge-secondary">{review.channel}</span>
                      </div>
                    </div>
                    <div className="review-rating">
                        <Rate allowHalf  disabled={true} defaultValue = { review.rating / 2 }/>
                    </div>
                    <div className="review-categories">
                      {review.categories.length > 0 ? (
                        <div className="category-tags">
                          {review.categories.slice(0, 3).map((cat, idx) => (
                            <span key={idx} className="category-tag">
                              {cat.category.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())}: {cat.rating}
                            </span>
                          ))}
                          {review.categories.length > 3 && (
                            <span className="category-tag">
                              +{review.categories.length - 3} more
                            </span>
                          )}
                        </div>
                      ) : (
                        <span className="no-categories">No categories</span>
                      )}
                    </div>
                    <div className="review-date">
                      {format(new Date(review.submittedAt), 'MMM dd, yyyy')}
                    </div>
                    <div className="review-status">
                      <button
                        className={`btn-toggle ${review.approved ? 'approved' : ''}`}
                        onClick={() => onToggleApproval(review.id)}
                      >
                        {review.approved ? 'Approved' : 'Pending'}
                      </button>
                    </div>
                  </div>
                  <div className="review-text">
                    <p>{review.publicReview}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </>
      )}
    </div>
  );
}

export default ReviewList;