import { useState, useCallback, useEffect } from 'react';
import { fetchReviews } from '../services/api';

/**
 * Custom hook to manage filter state and operations
 */
export const useFilters = (reviews = []) => {
  const [filters, setFilters] = useState({
    listing: '',
    rating: '',
    category: '',
    channel: '',
    type: '',
    dateRange: 'all'
  });

  const [sortBy, setSortBy] = useState('date');
  const [uniqueListings, setUniqueListings] = useState([]);
  const [uniqueTypes, setUniqueTypes] = useState([]);
  const [uniqueChannels, setUniqueChannels] = useState([]);
  const [allCategories, setAllCategories] = useState([]);
  const [loadingFilters, setLoadingFilters] = useState(true);

  const loadFilterOptions = useCallback(async () => {
    try {
      setLoadingFilters(true);
      const data = await fetchReviews({}, 'date');
      const options = data && data.filterOptions ? data.filterOptions : {};
      setUniqueListings(options.listings || []);
      setUniqueTypes(options.types || []);
      setUniqueChannels(options.channels || []);
      setAllCategories(options.categories || []);
    } catch (err) {
      console.error('Failed to load filter options:', err);
      // Fallback to empty arrays if fetch fails
      setUniqueListings([]);
      setUniqueTypes([]);
      setUniqueChannels([]);
      setAllCategories([]);
    } finally {
      setLoadingFilters(false);
    }
  }, []);

  // Fetch filter options from backend on mount
  useEffect(() => {
    loadFilterOptions();
  }, [loadFilterOptions]);

  const updateFilter = useCallback((key, value) => {
    setFilters(prev => ({
      ...prev,
      [key]: value
    }));
  }, []);

  const resetFilters = useCallback(() => {
    setFilters({
      listing: '',
      rating: '',
      category: '',
      channel: '',
      type: '',
      dateRange: 'all'
    });
    setSortBy('date');
  }, []);

  return {
    filters,
    sortBy,
    updateFilter,
    setSortBy,
    resetFilters,
    uniqueListings,
    allCategories,
    uniqueTypes,
    uniqueChannels,
    loadingFilters
  };
};
