import React, { useState, useEffect, useRef } from 'react';
import { useParams, Link } from 'react-router-dom';
import { fetchReviews } from '../services/api';
import { format } from 'date-fns';
import './PropertyPage.css';
import { Spin, Rate, Button, Row, Col, Card, Divider } from 'antd';
import { Modal } from 'antd';
import { StarTwoTone  } from '@ant-design/icons';

function PropertyPage() {
  const { listingName } = useParams();
  const [reviews, setReviews] = useState([]);
  const [expandedReviews, setExpandedReviews] = useState({});
  const [loading, setLoading] = useState(true);
  const reviewsSectionRef = useRef(null);
  const [modalVisible, setModalVisible] = useState(false);
  const decodedListingName = decodeURIComponent(listingName);

  const loadReviews = async () => {
    try {
      setLoading(true);
      const data = await fetchReviews();
      const allReviews = (data && data.reviews) || [];
      const propertyReviews = allReviews
        .filter(review => review.listingName === decodedListingName && review.approved)
        .sort((a, b) => new Date(b.submittedAt) - new Date(a.submittedAt));
      setReviews(propertyReviews);
      setLoading(false);
    } catch (error) {
      console.error('Failed to load reviews:', error);
      setLoading(false);
    }
  };
  const maxTextLength = 30;
  useEffect(() => {
    loadReviews();
  }, [decodedListingName]);

  const scrollToReviews = () => {
    if (reviewsSectionRef.current) {
      reviewsSectionRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const toggleExpand = (id) => {
    setExpandedReviews(prev => ({ ...prev, [id]: !prev[id] }));
  };

  const averageRating =
    reviews.filter(r => r.rating).length > 0
      ? (
          reviews
            .filter(r => r.rating)
            .reduce((sum, r) => sum + r.rating, 0) /
          reviews.filter(r => r.rating).length
        ).toFixed(2)
      : null;

  return (
    <div className="property-page">
      <Spin spinning={loading} tip="Loading reviews...">
        <div className="container">
          <Link to="/" className="back-link">
             Back to Dashboard
          </Link>
          <header className="property-header">
            <h1>{decodedListingName}</h1>
            <div className="property-meta">
              {averageRating && (
                <div className="property-rating">
                  <span className="review-count">
                    <StarTwoTone twoToneColor="#f5df1dff" />
                      {averageRating}
                  </span>
                </div>
              )}
              <div
                className="review-count"
                style={{ textDecoration: 'underline', cursor: 'pointer' }}
                onClick={scrollToReviews}
              >
                {reviews.length} reviews
              </div>
            </div>
          </header>
          <Row>
            <Col span={15}>
              ...
              <Divider />
              {reviews.length === 0 ? (
                <div className="no-reviews-message">
                  <p>No approved reviews available for this property.</p>
                </div>
              ) : (
                <div className="reviews-section" ref={reviewsSectionRef}>
                  <h2>
                    Guest Reviews
                    <StarTwoTone twoToneColor="#f5df1dff" style={{ marginLeft: "10px" }} /> {averageRating}(10)
                  </h2>

                  <div className="reviews">
                    {reviews.map(review => (
                      <div key={review.id} className="review-card">
                        <div className="review-card-header">
                          <div className="review-rating-stars">
                            {review.rating && (
                              <Rate disabled allowHalf defaultValue={review.rating / 2} />
                            )}
                          </div>
                          <div className="review-guest-info">
                            <span className="guest-name">{review.guestName}</span>
                            <span className="separator"></span>
                            <span className="review-month">{format(new Date(review.submittedAt), 'MMMM yyyy')}</span>
                          </div>
                        </div>
                        <div className='review-card-content'>
                          {(() => {
                            const text = review.publicReview || '';
                            const isExpanded = !!expandedReviews[review.id];
                            if (text.length <= maxTextLength) return text;
                            return (
                              <>
                                {isExpanded ? text : `${text.slice(0, maxTextLength)}... `}
                                <button
                                  type="button"
                                  className="see-more-btn"
                                  onClick={() => toggleExpand(review.id)}
                                >
                                  {isExpanded ? 'Hide' : 'Show more'}
                                </button>
                              </>
                            );
                          })()}
                        </div>
                      </div>
                    ))}
                  </div>
                  <Button style={{ marginTop: '20px', borderRadius: '10px', cursor: 'pointer', textAlign: 'center' }} onClick={() => setModalVisible(true)}>Show all {reviews.length} reviews</Button>

                  <Modal
                    title={`Reviews (${reviews.length})`}
                    open={modalVisible}
                    onCancel={() => setModalVisible(false)}
                    footer={null}
                    width={800}
                  >
                    <div className="modal-reviews">
                      {reviews.map(review => (
                        <div key={`modal-${review.id}`} className="review-card">
                          <div className="review-card-header">
                            <div className="review-rating-stars">
                              {review.rating && (
                                <Rate disabled allowHalf defaultValue={review.rating / 2} />
                              )}
                            </div>
                            <div className="review-guest-info">
                              <span className="guest-name">{review.guestName}</span>
                              <span className="separator"></span>
                              <span className="review-month">{format(new Date(review.submittedAt), 'MMMM yyyy')}</span>
                            </div>
                          </div>
                          <div className='review-card-content'>
                            {(() => {
                              const text = review.publicReview || '';
                              // longer truncate for modal
                              const isExpanded = !!expandedReviews[review.id];
                              if (text.length <= maxTextLength) return text;
                              return (
                                <>
                                  {isExpanded ? text : `${text.slice(0, maxTextLength)}... `}
                                  <button
                                    type="button"
                                    className="see-more-btn"
                                    onClick={() => toggleExpand(review.id)}
                                  >
                                    {isExpanded ? 'Hide' : 'Show more'}
                                  </button>
                                </>
                              );
                            })()}
                          </div>
                        </div>
                      ))}
                    </div>
                  </Modal>
                </div>
              )}
            </Col>
            <Col span={2}/>
            <Col span={7}>
              <div className="sidebar-card-wrap">
                <Card className="price-card">
                  <h3>206 / night</h3>
                  ...
                </Card>
              </div>
            </Col>
          </Row>
          
        </div>
      </Spin>
    </div>
  );
}

export default PropertyPage;

