import React, { useMemo } from 'react';
import { Link } from 'react-router-dom';
import { DashboardOutlined, CheckCircleOutlined, StarOutlined, HomeOutlined } from '@ant-design/icons';
import './StatsOverview.css';

function StatsOverview({ reviews = [], approvedReviews = [] }) {
  const stats = useMemo(() => {
    const total = reviews.length;
    const approved = approvedReviews.length;
    const averageRating =
      reviews.filter(r => r.rating).reduce((sum, r) => sum + r.rating, 0) /
      reviews.filter(r => r.rating).length || 0;

    const byListing = {};
    reviews.forEach(review => {
      if (!byListing[review.listingName]) {
        byListing[review.listingName] = {
          count: 0,
          totalRating: 0,
          ratingsCount: 0,
          approved: 0
        };
      }
      byListing[review.listingName].count++;
      if (review.rating) {
        byListing[review.listingName].totalRating += review.rating;
        byListing[review.listingName].ratingsCount++;
      }
      if (review.approved) {
        byListing[review.listingName].approved++;
      }
    });

    Object.keys(byListing).forEach(listing => {
      const data = byListing[listing];
      data.averageRating =
        data.ratingsCount > 0 ? (data.totalRating / data.ratingsCount).toFixed(1) : 'N/A';
    });

    const categoryIssues = {};
    reviews.forEach(review => {
      review.categories.forEach(cat => {
        if (!categoryIssues[cat.category]) {
          categoryIssues[cat.category] = { total: 0, lowRatings: 0 };
        }
        categoryIssues[cat.category].total++;
        if (cat.rating < 4) {
          categoryIssues[cat.category].lowRatings++;
        }
      });
    });

    const topIssues = Object.entries(categoryIssues)
      .map(([category, data]) => ({
        category,
        percentage: ((data.lowRatings / data.total) * 100).toFixed(1)
      }))
      .sort((a, b) => parseFloat(b.percentage) - parseFloat(a.percentage))
      .slice(0, 3);

    return {
      total,
      approved,
      averageRating: averageRating,
      byListing,
      topIssues
    };
  }, [reviews, approvedReviews]);

  return (
    <div className="stats-overview">
      <div className="stats-grid">
        <div className="stat-card">
          <div className="stat-card-header">
            <DashboardOutlined className="stat-icon" />
            <div className="stat-label">Total Reviews</div>
          </div>
          <div className="stat-value">{stats.total}</div>
        </div>
        <div className="stat-card">
          <div className="stat-card-header">
            <CheckCircleOutlined className="stat-icon approved-icon" />
            <div className="stat-label">Approved for Display</div>
          </div>
          <div className="stat-value">{stats.approved}</div>
        </div>
        <div className="stat-card">
          <div className="stat-card-header">
            <StarOutlined className="stat-icon star-icon" />
            <div className="stat-label">Average Rating</div>
          </div>
          <div className="stat-value">
            {stats.averageRating}
          </div>
          {/* <div className="stat-subtext">out of 10.0</div> */}
        </div>
        <div className="stat-card">
          <div className="stat-card-header">
            <HomeOutlined className="stat-icon" />
            <div className="stat-label">Properties</div>
          </div>
          <div className="stat-value">{Object.keys(stats.byListing).length}</div>
        </div>
      </div>

      <div className="stats-sections">
        <div className="card">
          <h3>Performance by Property</h3>
          <div className="listing-stats">
            {Object.entries(stats.byListing).map(([listing, data]) => (
              <div key={listing} className="listing-stat-item">
                <Link
                  to={`/property/${encodeURIComponent(listing)}`}
                  className="listing-name-link"
                >
                  {listing}
                </Link>
                <div className="listing-metrics">
                  <span>{data.count} reviews</span>
                  <span>Avg: {data.averageRating}</span>
                  <span>{data.approved} approved</span>
                </div>
              </div>
            ))}
          </div>
        </div>

        {stats.topIssues.length > 0 && (
          <div className="card">
            <h3>Recurring Issues</h3>
            <div className="issues-list">
              {stats.topIssues.map(issue => (
                <div key={issue.category} className="issue-item">
                  <div className="issue-category">{issue.category.replace(/_/g, ' ')}</div>
                  <div className="issue-percentage">{issue.percentage}% low ratings</div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default StatsOverview;

