import React from 'react';
import { DashboardOutlined } from '@ant-design/icons';
import { MainLayout } from '../components/layout';
import ReviewList from '../components/ReviewList';
import FilterBar from '../components/FilterBar';
import StatsOverview from '../components/StatsOverview';
import { useReviews, useFilters } from '../hooks';
import './DashboardPage.css';

function DashboardPage() {
  const { filters, sortBy, updateFilter, setSortBy, uniqueListings, allCategories, uniqueTypes, uniqueChannels } = useFilters([]);
  const { reviews, loading, approvedReviews, toggleApproval, bulkApprove } = useReviews(filters, sortBy);

  const handleFilterChange = (key, value) => {
    updateFilter(key, value);
  };

  return (
    <div className="dashboard-page">
      <MainLayout loading={loading}>
        <div className="dashboard-header-override">
          <div className="header-title">
            <h1><DashboardOutlined style={{ marginRight: 8, color: '#2563eb' }} />Reviews Dashboard</h1>
          </div>
        </div>

        <StatsOverview reviews={reviews} approvedReviews={approvedReviews} />

        <FilterBar
          filters={filters}
          setFilters={handleFilterChange}
          sortBy={sortBy}
          setSortBy={setSortBy}
          uniqueListings={uniqueListings}
          allCategories={allCategories}
          uniqueTypes={uniqueTypes}
          uniqueChannels={uniqueChannels}
        />

        <ReviewList
          reviews={reviews}
          approvedReviews={approvedReviews}
          onToggleApproval={toggleApproval}
          onBulkApprove={bulkApprove}
        />
      </MainLayout>
    </div>
  );
}

export default DashboardPage;
